/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f8f9fa; /* Light background color */
}

header {
  background-color: #2874f0; /* Flipkart's primary color */
  color: #fff;
  padding: 15px 20px; /* Increased padding */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 36px;
  color: #fff;
}

.search-bar {
  display: flex;
  align-items: center;
}

.search-bar input {
  padding: 8px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  font-size: 16px; /* Larger font size */
}

.search-bar button {
  padding: 8px 15px; /* Increased padding */
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

nav {
  background-color: #2874f0; /* Flipkart's primary color */
  color: #fff;
  padding: 15px 20px; /* Increased padding */
}

nav ul {
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin-right: 20px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  transition: color 0.3s;
  font-size: 18px; /* Larger font size */
}

nav ul li a:hover {
  color: #ffd700; /* Highlight color */
}

.container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px;
}

.categories {
  flex-basis: calc(20% - 20px); /* Adjusted width */
  margin-bottom: 20px; /* Increased margin */
}

.categories h2 {
  margin-bottom: 15px; /* Increased margin */
  font-size: 24px; /* Larger font size */
}

.categories ul li {
  margin-bottom: 10px; /* Increased margin */
  font-size: 16px; /* Larger font size */
}

.products {
  flex-basis: calc(80% - 20px); /* Adjusted width */
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.product {
  flex-basis: calc(33.33% - 20px);
  border: 1px solid #ccc;
  padding: 20px; /* Increased padding */
  transition: box-shadow 0.3s;
}
.header {
  background-color: #343a40;
  color: white;
  padding: 10px 0;
  text-align: center;
}

.navbar-brand img {
  height: 40px;
}

.card-img-top {
  height: 200px;
  object-fit: cover;
}

.card-body {
  text-align: center;
}

footer {
  background-color: #343a40;
  color: white;
}

footer h4 {
  margin-bottom: 15px;
}

footer p {
  margin-bottom: 10px;
}


.product:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2); /* Enhanced shadow on hover */
}

.product img {
  width: 100%;
  height: auto;
}

footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 15px 0; /* Increased padding */
}
/* Navbar brand */
.navbar-brand img {
  max-height: 40px;
}

/* Navbar toggler icon */
.navbar-toggler-icon {
  background-color: white; /* Change color as needed */
}

/* Active page link */
.navbar-nav .nav-item.active .nav-link {
  font-weight: bold;
}

/* Dropdown icon */
.navbar-nav .dropdown-toggle::after {
  display: none; /* Remove default caret */
  /*content: url('caret-down.png');  Replace with your custom caret icon */
  margin-left: 5px; /* Adjust spacing as needed */
}

/* Dropdown menu */
.dropdown-menu {
  border: none; /* Remove default border */
  border-radius: 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Add shadow for depth */
}

.dropdown-item {
  color: #333; /* Adjust text color as needed */
}

.dropdown-item:hover,
.dropdown-item:focus {
  background-color: #f8f9fa; /* Highlight on hover/focus */
}

/* Account dropdown icon */
.navbar-nav .dropdown-toggle#accountDropdown::after {
  display: none; /* Remove default caret */
}

/* Cart icon */
.navbar-nav .fa-shopping-cart {
  margin-right: 5px; /* Adjust spacing as needed */
}

/* Account icon */
.navbar-nav .fa-user {
  margin-right: 5px; /* Adjust spacing as needed */
}
/* Footer */
.footer {
  background-color: #343a40; /* Dark background color */
  color: #ffffff; /* Text color */
  padding-top: 20px; /* Top padding */
  padding-bottom: 20px; /* Bottom padding */
}

.footer p {
  margin-bottom: 5px; /* Adjust spacing between paragraphs */
}

.footer a {
  text-decoration: none; /* Remove underline */
  font-weight: bold; /* Bold text */
}

.footer a:hover {
  text-decoration: underline; /* Underline on hover */
}

/* Product Cards */
.card {
  margin-bottom: 20px; /* Add space between cards */
  border: 1px solid #dee2e6; /* Add border */
  border-radius: 8px; /* Add border radius */
}

.card-img-top {
  border-top-left-radius: 8px; /* Rounded corners for image */
  border-top-right-radius: 8px;
}

.card-title {
  font-size: 1.2rem; /* Increase title font size */
  margin-bottom: 10px; /* Add space below title */
}

.card-text {
  margin-bottom: 10px; /* Add space below text */
}

.btn-group {
  margin-right: 10px; /* Add space between buttons */
}

/* Adjust rating text position */
.card-body .text-muted {
  margin-top: 5px;
}

/* Hover effect on product cards */
.card:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Add shadow on hover */
  transition: box-shadow 0.3s ease-in-out; /* Smooth transition */
}

/* Style buttons */
.btn-outline-secondary {
  color: #6c757d; /* Button text color */
  border-color: #6c757d; /* Button border color */
}

.btn-outline-secondary:hover {
  color: #fff; /* Change text color on hover */
  background-color: #6c757d; /* Change background color on hover */
  border-color: #6c757d; /* Change border color on hover */
}

.btn-primary {
  background-color: #007bff; /* Primary button background color */
  border-color: #007bff; /* Primary button border color */
}

.btn-primary:hover {
  background-color: #0056b3; /* Darker background color on hover */
  border-color: #0056b3; /* Darker border color on hover */
}
